.cards{display:flex;flex-wrap:wrap;gap:30px;align-items:center;justify-content:center}.card_wrapper{width:320px;height:510px}.card_content{width:100%;height:100%;background-color:#f2f2f2;border-radius:12px;clip-path:polygon(15% 0, 80% 0%, 100% 0, 100% 80%, 100% 100%, 0 100%, 0% 80%, 0 11%);padding:20px 50px;background-image:url("../../assets/Cat.png");background-repeat:no-repeat;background-position:left 0 bottom -17px;position:relative}.card_content_wrapper{background:#1698d9;width:100%;height:480px;border-radius:12px;padding:4px;clip-path:polygon(15% 0, 80% 0%, 100% 0, 100% 80%, 100% 100%, 0 100%, 0% 80%, 0 11%);cursor:pointer}.card_content_wrapper:hover{background:#22a7e9}.card_content_wrapper:hover .card_weight{background-color:#22a7e9}.card_desc{font-size:16px;color:#666;margin-bottom:5px}.card_desc:before{content:"Сказочное заморское яство"}.card_title{font-size:48px;font-weight:700}.card_subtitle{font-size:24px;margin-bottom:15px}.card_features{color:#666;font-size:14px;font-weight:700}.card_features span{font-weight:400}.card_weight{height:80px;width:80px;border-radius:50%;background-color:#1698d9;position:absolute;right:12px;bottom:12px;font-size:42px;display:flex;align-items:center;justify-content:space-between;padding:20px 0 5px 0;color:#fff;flex-direction:column;line-height:22px}.card_weight span{font-size:22px}.card_footer{font-size:13px;color:#fff;text-align:center;margin-top:14px}.card_footer span{color:#1698d9;text-decoration:underline dotted;cursor:pointer}.card_footer span:hover{color:#22a7e9}.selected .card_weight{background-color:#d91667}.selected .card_content_wrapper{background-color:#d91667}.selected .card_content_wrapper:hover{background-color:#e52e7a}.selected .card_content_wrapper:hover .card_weight{background-color:#e52e7a}.selected .card_content_wrapper:hover .card_desc{color:#e62e7a}.selected .card_content_wrapper:hover .card_desc:before{content:"Котэ не одобряет?"}.disabled .card_weight{background-color:#b3b3b3 !important}.disabled .card_content_wrapper{background-color:#b3b3b3 !important;cursor:not-allowed;position:relative}.disabled .card_content_wrapper:before{content:"";position:absolute;background:#fff;opacity:.5;width:100%;height:100%;border-radius:12px;clip-path:polygon(15% 0, 80% 0%, 100% 0, 100% 80%, 100% 100%, 0 100%, 0% 80%, 0 11%);z-index:1;left:0;top:0}.disabled .card_footer{color:#ff6}