@font-face {
  font-family: "Trebuchet MS";
  src: local('./fonts/Trebuchet MS Regular'), url("./fonts/TrebuchetMS.ttf") format("truetype"),
    url("./fonts/TrebuchetMS.eot?#iefix") format("embedded-opentype"),
    url("./fonts/TrebuchetMS.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Trebuchet MS";
  src: local("./fonts/Trebuchet MS Bold"),
    url("./fonts/TrebuchetMS-Bold.ttf") format("truetype"),
    url("./fonts/TrebuchetMS-Bold.eot?#iefix") format("embedded-opentype"),
    url("./fonts/TrebuchetMS-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Exo2.0";
  src: local("Exo2.0 Thin"), url("./fonts/Exo2.0-Thin.ttf") format("truetype"),
    url("./fonts/Exo2.0-Thin.eot") format("embedded-opentype"),
    url("./fonts/Exo2.0-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
}
